.slider {
    position: relative;
    height: 100%;
    width: 100%;
}

.slider.none {
    display: none;
}

.slide {
    width: 100%;
    height: 100%;
    -webkit-animation: topdown 1s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
            animation: topdown 1s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
}

#prev, #next {
    position: absolute;
    font-size: 4rem;
    top: 20%;
    color: black;
    opacity: 0.8;
    cursor: pointer;
}


#prev {
    left: 0;
}

#next {
    right: 0;
}

@-webkit-keyframes topdown {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes topdown {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}


